.wool-section {
  padding: 160px 0 80px;

  @include m-1070 {
    padding: 80px 0;
  }

  // .wool-section__container

  &__container {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 2rem;

    @include m-1070 {
      grid-template-columns: repeat(2, 1fr);
      grid-template-areas: "a b" "a c";

    }

    @include m-680 {
      display: block;
    }
  }
}


.wool {
  position: relative;
  border-radius: var(--br);
  padding: 55px 64px 64px;
  display: flex;

  @include m-1070 {
    padding: 30px
  }

  @include m-680 {
    margin-bottom: 1rem;
  }

  // .wool__content

  &__content {
    h3 {
      margin-bottom: 16px;
      font-size: 2.1rem;
      font-weight: 400;
      line-height: 120%;

      @include m-1070 {
        font-size: 1.8rem;
      }

      span {
        padding: 3px 18px 4px;
        background-color: #fff;
        border-radius: 40px;
        white-space: nowrap;
        display: inline-block;
        vertical-align: top;

      }
    }

    p {
      font-size: 1.3rem;
      font-weight: 400;
      line-height: 140%;
      letter-spacing: .02em;
      opacity: .5;

      @include m-1070 {
        font-size: 1.1rem;
      }
    }

    .btn {
      margin-top: 30px;

      @include m-1070 {
        margin-top: 10px;
      }
    }
  }

  &--man {
    padding: 55px 45% 55px 55px;
    background-color: var(--grey-color);
    grid-column: 4 span;
    flex-direction: column;
    gap: 20px;

    @include m-1070 {
      padding: 55px 64px 0;
      padding: 30px 30px 0;
      overflow: hidden;
      grid-area: a;
    }

    h3 {
      span {
        background-color: var(--dark-color-08);
        color: var(--white-color);
        transform: rotate(-5deg) translate(-30px, -14px);
        white-space: nowrap;

        @include m-1070 {
          transform: rotate(-5deg) translate(-15px, -10px);
        }
      }
    }

    // .wool__img

    .wool__img {
      position: absolute;
      right: -30px;
      bottom: 0;
      display: flex;
      width: 50%;

      @include m-1070 {
        position: relative;
        width: 100%;
        height: 320px;
        right: 0;
        flex: 1;

        align-content: end;
        flex-wrap: wrap;
        align-self: center;
      }

      @include m-680 {
        justify-content: center;
        align-content: center;
      }
    }
  }

  &--price {
    background-color: var(--pink-color);
    grid-column: 3 span;

    @include m-1070 {
      grid-area: b;
    }

    ul li {
      background-color: var(--pink-light-color);
      border-radius: 40px;
      padding: 5px 18px;
      display: inline-block;
      margin-top: 3px;
    }

  }

  &--consultation {
    background-color: var(--violet);
    position: relative;
    grid-column: 7 span;
    color: var(--white-color);
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      background: url("../img/svg/dot.svg") 0 0 / cover no-repeat;
      width: 280px;
      height: 280px;
      right: -50px;
      bottom: -50px;
      transform: rotate(40deg);
      opacity: 0.4;

      @include m-1070 {
        width: 140px;
        height: 140px;
        right: -20px;
        bottom: -20px;
      }
    }

    @include m-1070 {
      grid-area: c;
    }

    h3 {
      margin-right: 1rem;
    }

    * {
      display: inline-block;
    }
  }
}