.stars-section {
  padding: 120px 0;
  background-color: var(--grey-color-07);
 }


.stars {
  position: relative;
  z-index: 1;

  &__img {
    z-index: -1;
    border-radius: var(--br-50);
    position: absolute;

    &:nth-child(1) {
      width: 100px;
      height: 100px;
      top: 116px;
      left: 33px;

      @include m-960 {
        opacity: .2;
      }

      @include m-650 {
        display: none;
      }
    }

    &:nth-child(2) {
      width: 90px;
      height: 90px;
      top: -100px;
      right: -36px;

      @include m-600 {
        right: 50%;
        transform: translateX(50%);
      }
    }

    &:nth-child(3) {
      width: 80px;
      height: 80px;
      bottom: 102px;
      left: 265px;

      @include m-1070 {
        opacity: .2;
      }

      @include m-650 {
        display: none;
      }
    }

    &:nth-child(4) {
      width: 70px;
      height: 70px;
      bottom: -64px;
      left: 87px;

      @include m-460 {
        left: 10px;
      }
    }

    &:nth-child(5) {
      width: 60px;
      height: 60px;
      top: 150px;
      right: 159px;

      @include m-1070 {
        opacity: .2;
      }

      @include m-650 {
        display: none;
      }
    }

    &:nth-child(6) {
      width: 50px;
      height: 50px;
      bottom: 87px;
      right: 54px;

      @include m-600 {
        right: 5px;
      }

      @include m-460 {
        display: none;
      }
    }

  }

  // .stars__header
  &__header {
    text-align: center;
    max-width: 1000px;
    margin: 0 auto 1rem;

    h2,
    h3 {
      line-height: 140%;

      span {
        color: var(--second-bright-color);
      }
    }

    h2 {
      font-size: 2.3rem;
    }

    h3 {
      font-size: 1.8rem;

      @include m-650 {
        font-size: 1.4rem;
        margin-top: 30px;
      }
    }
  }

  // .stars__main

  &__main {
    max-width: 600px;
    margin: 0 auto 3rem;
    text-align: center;
    color: var(--dark-color-06);
  }

  // .stars__btn

  &__btn {
    margin: 0 auto 3rem;
    text-align: center;
  }

  // .stars__footer

  &__footer {
    text-align: center;

    p {
      font-size: .8rem;
      color: var(--dark-color-04);
      margin: 0;
    }

    img {
      width: 120px;
    }
  }
}
