.page {
  box-sizing: border-box;
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  font-size: 18px;

  @include m-768 {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  scroll-behavior: smooth;

  &:target {
    scroll-margin-top: 6rem;
  }
}

.page__body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  font-size: 100%;
  letter-spacing: .8px;
  line-height: 140%;
  color: var(--dark-color);
  background-color: var(--white-color);
  position: relative;

  &.lock {
    overflow: hidden;
  }

  *::-webkit-scrollbar {
    width: 12px;
    height: 12px;
  }

  *::-webkit-scrollbar-thumb {
    background-color: var(--blue-dark-color);
    // border-radius: var(--br-3);
    border-radius: 99px;
  }

  *::-webkit-scrollbar-track {
    background: var(--grey-color);
  }

  *::-webkit-scrollbar-button {
    height: 16px;
  }

}

h1,
h2,
h3,
h4,
h5,
h6 {
  letter-spacing: 1px;
  margin: 0;
  padding: 0;
  line-height: 120%;
}

h1 {
  font-size: 1.6rem;
}

h2 {
  font-size: 1.4rem;
}

h3 {
  font-size: 1.2rem;
}

h4,
h5,
h6 {
  font-size: 1.1rem;
}

p {
  color: inherit;
  font-weight: inherit;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  vertical-align: middle;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  // overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .main {
    flex: 1;
    margin-top: 134px;
  }
}

.is-hidden {
  display: none !important;
}

.btn-reset,
button {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset,
input {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
