*[data-grid="2"] {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

*[data-grid="3"] {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

*[data-grid-span="2"] {
  grid-column: span 2;
}

*[data-grid-span="3"] {
  grid-column: span 3;
}


.content {

  p {
    margin-bottom: 0.7rem;
    line-height: 160%;

    span {
      display: inline-block;
      background-color: var(--pink-color-04);
      padding: .1rem .4rem .2rem;
      border-radius: .2rem;
    }
  }

  h2,
  h3 {
    font-size: 2rem;
    color: var(--second-color);
    margin-bottom: 2rem;

    span {
      color: var(--dark-color-06);
      display: block;
      font-size: 1.3rem;
    }
  }

  h4,
  h5,
  h6 {
    margin: 3rem 0 1.7rem;
  }

  blockquote {
    margin: 0;
    display: inline-block;
    background-color: var(--blue-light-color-02);
    padding: 1rem;
    border-radius: var(--br-2);
    font-size: 1.2rem;
  }

  ul,
  ol {
    margin: 1rem 0;
  }

  ol {
    padding: 0;
    counter-reset: item;
    list-style-type: none;

    li {
      margin-bottom: 0.7rem;

      &::before {
        float: left;
        display: block;
        content: counter(item, decimal-leading-zero) " ";
        counter-increment: item;
        background-color: var(--second-bright-color);
        color: var(--white-color);
        text-align: center;
        padding: .2rem .4rem;
        width: 3rem;
        font-size: .9rem;
        margin-right: 10px;
        border-radius: .3rem;
      }
    }
  }

  ul {
    padding-left: 3rem;

    li {
      margin: 1rem;
      padding-left: 1rem;
      position: relative;

      &::after {
        content: '✔';
        position: absolute;
        top: 0;
        left: 0;
        color: var(--second-bright-color);
      }

      &::before {
        display: none;
      }
    }
  }
}

.fx {
  display: flex;
  align-items: center;

  &--c {
    justify-content: center;
  }

  &--g5 {
    gap: 5px;
  }
}

.bx-check-double {
  color: var(--second-color);
}

.bx-x {
  color: var(--red-dark-color);
}

div[class$="__title"] {

  h1,
  h2,
  h3 {
    font-size: 2.3rem;
    line-height: 120%;

    @include m-768 {
      text-align: center;
    }

    span {
      color: var(--second-bright-color);
    }
  }
}

