.tariffs-section__title {
  margin: 3rem 0 2rem;
}

.price-card {

  // .price-card__list

  &__list {


    &>li {
      border-radius: var(--br);
      border: 3px solid var(--blue-dark-color);
            margin-bottom: 2rem;
      transition: all .3s ease-in-out;

      @include hover {
        border: 3px solid var(--violet);
        .price-card__desc {
          // color: var(--dark-color-08);
          background-color: var(--violet);
        }
      }
    }
  }

  // price-card__header
  &__header {
    display: grid;
    grid-template-columns: 1fr 3fr;
    text-align: center;

    @include m-860 {
      grid-template-columns: 1fr 2fr;
    }

    @include m-680 {
      display: block;
    }
  }

  // price-card__title{
  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-left: 2rem;
    padding-right: 2rem;

    h4 {
      font-size: 2rem;
      line-height: 120%;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;

      @include m-560 {
        font-size: 1.7rem;
      }
    }
  }

  // price-card__text
  &__text {
    color: var(--dark-color-06);
    text-align: left;
    flex: 1;

    @include m-680 {
      text-align: center;
    }

    p {
      font-size: 0.9rem;
      margin-bottom: 0.4rem;

      @include m-680 {
        font-size: 1rem;
      }
    }
  }

  // price-card__desc
  &__desc {
    color: var(--white-color);
    font-weight: 400;
    font-size: .9rem;
    background-color: var(--blue-dark-color);
    padding: 6px 14px;
    border-radius: 0 0 var(--br-3) var(--br-3);
    display: block;
    max-width: 300px;
    line-height: 120%;
    transition: all .3s ease-in-out;
    margin: 0 auto 1rem;

    @include m-680 {
      border-radius: var(--br-3);
    }

  }

  // .price-card__img

  &__img {
    padding: 1rem 0 0 1rem;

    @include m-680 {
      padding: 1rem;
    }

    img {
      border-radius: var(--br);
      height: 100%;

      @include m-650 {
        width: 300px;
      }
    }
  }

  // .price-card__content

  &__content {
    padding: 2rem;

    button {
      @include m-768 {
        margin: 0 auto;
      }
    }

    h5 {
      font-weight: 600;
      font-size: 1.3rem;
      color: var(--second-color);

      @include m-768 {
        text-align: center;
      }
    }

    ul {
      display: flex;
      gap: 2rem;

      @include m-680 {
        display: block;
        // margin-bottom: 2rem !important;
      }

      li {
        flex: 1;
        margin: 1rem 0;
        background-color: transparent;
        border-radius: var(--br-3);
        transition: all .2s ease-in-out;
        position: relative;
        width: 100%;

        @include m-680 {
          margin-bottom: 3rem;
        }


        ul {
          display: block;
          margin: 1.3rem 0 2rem;

          @include m-680 {
            display: flex;
            flex-wrap: wrap;
            gap: 0;
          }

          @include m-520 {
            display: block;
          }

          li {
            padding: 0;
            margin: 0;
            padding: .05rem;
            margin-bottom: .5rem;
            margin-top: .5rem;
            font-size: .9rem;
            display: flex;
            align-items: center;

            @include m-680 {
              padding: 0.2rem 0;
              margin-bottom: 0;
              margin-top: 0;
              flex: 0 0 50%;
            }

            i {
              font-size: 1.4rem;
              margin: 0 .9rem;
            }

          }


        }

      }
    }
  }

  // price-card__course-price

  &__course-price {
    font-size: 2.3rem;
    margin: 2rem 0;
    font-weight: 700;
    color: var(--second-color);

    @include m-768 {
      text-align: center;
    }

    span {
      font-weight: 400;
      font-size: 1.1rem;
      color: var(--blue-dark-color);

      &::before {
        content: '/ ';
      }
    }
  }

  // .price-card__course-btn
  &__course-btn {
    @include m-768 {
      display: flex;
      justify-content: center;
    }

  }
}
