// ==========Подложка по моб.меню===========
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;

  &.open {
    opacity: 1;
    visibility: visible;
    transition: all .3s;
  }
}

// Гамбургер
// .header__hamburger
.hamburger-slim {
  display: none;

  @include m-1070 {
    display: block;
    position: absolute;
    z-index: 600;
    top: 35px;
    right: 20px;
    width: 40px;
    height: 40px;

    .nav-icon {
      width: 30px;
      height: 30px;
      position: relative;
      transition: .1s;
      // margin: 10px 10px;
      cursor: pointer;
      display: inline-block;

      &::before {
        content: '';
        position: absolute;
        z-index: -1;
        left: -10px;
        top: -10px;
        width: 50px;
        height: 50px;
        background-color: var(--white-color);
        border-radius: var(--br-50);
      }
    }

    .nav-icon span {
      width: 5px;
      height: 5px;
      background-color: var(--second-color);
      display: block;
      border-radius: var(--br-50);
      position: absolute;
      // z-index: 600;
    }

    .nav-icon:hover span {
      transform: scale(1.2);
      transition: 350ms cubic-bezier(.8, .5, .2, 1.4);
    }

    .nav-icon span:nth-child(1) {
      left: 0;
      top: 0;
    }

    .nav-icon span:nth-child(2) {
      left: 12px;
      top: 0;
    }

    .nav-icon span:nth-child(3) {
      right: 0;
      top: 0;
    }

    .nav-icon span:nth-child(4) {
      left: 0;
      top: 12px;
    }

    .nav-icon span:nth-child(5) {
      position: absolute;
      left: 12px;
      top: 12px;
    }

    .nav-icon span:nth-child(6) {
      right: 0px;
      top: 12px;
    }

    .nav-icon span:nth-child(7) {
      left: 0px;
      bottom: 0px;
    }

    .nav-icon span:nth-child(8) {
      position: absolute;
      left: 12px;
      bottom: 0px;
    }

    .nav-icon span:nth-child(9) {
      right: 0px;
      bottom: 0px;
    }

    .nav-icon.open {
      transform: rotate(180deg);
      cursor: pointer;
      transition: .2s cubic-bezier(.8, .5, .2, 1.4);
    }

    .nav-icon.open span {
      border-radius: var(--br-50);
      transition-delay: 200ms;
      transition: .5s cubic-bezier(.8, .5, .2, 1.4);
    }

    .nav-icon.open span:nth-child(2) {
      left: 6px;
      top: 6px;
    }

    .nav-icon.open span:nth-child(4) {
      left: 6px;
      top: 18px;
    }

    .nav-icon.open span:nth-child(6) {
      right: 6px;
      top: 6px;
    }

    .nav-icon.open span:nth-child(8) {
      left: 18px;
      bottom: 6px;
    }
  }
}

//  ===========Header=================
.header-section {
  position: relative;
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 50;
  background: var(--white-color);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

// Весь контент header
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
  min-height: 60px;
  padding: 0.8rem 0;

  * {
    align-items: center;
  }

  // Лого
  // .header-logo
  .header-logo {
    display: block;
    width: 170px;
    height: 70px;
  }

  // Меню
  // .header__menu
  &__menu {
    flex: 1;
    width: 100%;

    @include m-1070 {
      display: none;
    }

    // .menu-content
    .menu-content {
      width: 100%;
    }
  }

  // Связаться
  // .header__connection
  &__connection {
    display: flex;
    align-items: center;
    margin-left: auto;
    gap: 18px;

    @include m-1070 {
      margin-right: 70px;
    }

    @include m-520 {
      display: none;
    }

    // Иконки соц
    // .header-socials
    .header-socials {
      display: flex;
      gap: 8px;

      // header-socials__icon
      &__icon {
        width: 30px;
        height: 30px;
        display: block;
        transition: transform .3s ease-in-out;

        @include hover {
          transform: scale(1.1);
        }
      }

      @include m-650 {
        display: none;
      }
    }
  }

}


// Меню навигация ============================

.nav {
  width: 100%;

  // .nav__list
  &__list {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    position: relative;

    @include m-1070 {
      flex-direction: column;
      flex-wrap: nowrap;
      align-items: start;
    }

    // .nav__list--dropdown
    &--dropdown {
      position: absolute;
      background-color: var(--white-color);
      left: 50%;
      top: 100%;
      transform: translateX(-50%);
      min-width: 280px;
      padding: 20px 16px 14px;
      display: block;
      box-shadow: 0px 1px 4px rgba(21, 21, 21, 0.02), 0px 8px 12px rgba(21, 21, 21, 0.08);
      border-radius: var(--br-2);
      z-index: 80;
      transition: all 0.3s;
      opacity: 0;
      visibility: hidden;

      @include m-1070 {
        position: relative;
        right: auto;
        left: 0;
        top: 0;
        padding: 0px;
        width: 100%;
        box-shadow: none;
        display: none;

        &.transformation {
          display: block;
          opacity: 1;
          visibility: visible;
          transform: none;
          border-radius: 0;
          box-shadow: none;
          transition: all .3s;
          background-color: transparent;
        }
      }
    }

  }

  // .nav__item

  &__item {
    position: relative;

    @include hover {
      .nav__list--dropdown {
        visibility: visible;
        opacity: 1;
        transition: all 0.3s;
      }
    }

    @include m-1070 {
      margin: 0;
      width: 100%;
      padding: 20px;
      border-bottom: 0.5px solid #D3D3D3;
    }

    // .nav__item--drop
    &--drop {
      position: static;

      @include m-1070 {
        position: relative;

        .dropdown-submenu {
          left: 0;
          top: 100%;
          transform: translateX(0);
          width: 100%;

          @include m-1070 {
            top: 0;
          }
        }
      }

    }

  }

  // .nav__link

  &__link {
    line-height: 120%;
    text-decoration: none;
    transition: .5s;
    font-size: 1rem;
    display: block;
    padding: 1rem .7rem;

    @include hover {
      transition: .5s;
    }

    @include m-1070 {
      color: var(--black-color);
      font-weight: 500;
      background-color: transparent;
      transition: background-color .4s ease-in;

      @include hover {
        background-color: var(--grey-lite-color);
      }

    }

    &.dropdown-list__link {
      font-weight: 400;
      // color: var(--grey-color);

      @include hover {
        color: var(--black-color);
        background-color: transparent;
      }

      @include m-1070 {
        padding-left: 45px;
      }
    }

    &.dropdown-submenu__list-item {
      padding: 0;
    }

    // .nav__link--drop
    &--drop {
      @include m-1070 {
        position: relative;

        &::after {
          content: "";
          width: 25px;
          height: 14px;
          background-image: url("../img/svg/arrow-b.svg");
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          display: block;
          margin-left: auto;
          transition: transform 0.3s;
          position: absolute;
          right: 30px;
          top: 50%;
          transform: translateY(-50%);
        }

        &.active::after {
          transform: rotate(180deg);
          transition: all .3s;
          top: calc(50% - 5px);
        }
      }
    }
  }
}

//!Ссылки во всем меню
.nav__link,
.dropdown-submenu__list-item a,
.dropdown-list__link {
  @include m-1070 {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

//! Выпадающее меню в виде списка, стилизания бордера внизу в моб.версии
.dropdown-list {
  flex-wrap: wrap;
  width: 100%;

  &__item {
    @include m-1070 {
      width: 100%;
    }

    &:not(:last-child) {
      border: none;
      border-bottom: 0.5px solid #D3D3D3;
    }

    &:last-child {
      @include m-1070 {
        border: none;
        // border-bottom: 0.5px solid #D3D3D3;
      }
    }
  }

  // .dropdown-list__link
  &__link {
    display: inline-block;
    line-height: 20px;
    color: #5D5D63;
    padding: 10px 0;
    transition: color .3s ease-in;

    @include hover {
      color: var(--black-color);
    }
  }
}

// Выпадающее меню
.dropdown-submenu {
  display: grid;

  @include m-1070 {
    display: none;
  }

  .dropdown-submenu__item {
    padding: 0;

    @include m-1070 {
      display: block;
      margin-bottom: 1rem;
      padding: 0;
    }

  }

  .box {
    position: relative;
    width: 100%;
    aspect-ratio: 3/2.3;
    border-radius: var(--br-2);
    overflow: hidden;
    display: block;
    transition: all .3s ease-in-out;
    background-color: var(--blue-light-color);
    box-shadow: var(box-shadow-min);

    &__title {
      position: absolute;
      z-index: 2;
      width: 100%;
      padding: 0 1rem;
      bottom: .8rem;

      @include m-1180 {
        padding: 0 .7rem;
      }

      h2,
      p {
        color: var(--white-color);
        letter-spacing: 1.1px;
        line-height: 120%;
        text-align: center;
      }

      h2 {
        font-weight: 500;
        font-size: 1.2rem;
        margin-bottom: .3rem;

        @include m-1180 {
          font-size: 1rem;
        }
      }

      p {
        font-size: .9rem;

        @include m-1180 {
          font-size: .8rem;
        }
      }
    }

    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(72, 76, 97, 0) 0%, var(--second-color) 90%);
      z-index: 2;
      bottom: 0;
      left: 0;
      transition: opacity .3s ease-in-out;
      opacity: .8;
    }

    @include hover {
      &:before {
        transition: opacity .3s ease-in-out;
        opacity: 1;
      }
    }

    img {
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 1;
    }
  }

  *[data-nav="box-button"] {
    .box {
      margin-bottom: 1rem;
      aspect-ratio: 3/1.5;
      position: relative;
      width: 100%;
      overflow: visible;
      border-radius: var(--br-2);
      background-color: var(--blue-light-color-05);
      transition: all 0.3s ease-in-out;

      @include m-1070 {
        aspect-ratio: 3/1;

        &::after {
          display: none;
        }
      }

      &::before {
        display: none;
      }

      &::after {
        content: "";
        position: absolute;
        width: 100px;
        height: 100px;
        right: -37px;
        top: -1.2rem;
        background: url(../img/svg/icon.svg) top/contain no-repeat;
        transition: all 0.3s ease-in-out;
        opacity: .2;
        transform: scale(0.8);
      }

      @include hover {
        background-color: var(--pink-color-04);

        &::after {
          opacity: .6;
        }
      }

      &__title {
        bottom: 50%;
        left: 0;
        transform: translateY(50%);

        h2 {
          font-size: 1.2rem;
          color: var(--dark-color);
          font-weight: 500;

          span {
            font-size: 1.4rem;
            font-weight: 600;
          }
        }
      }


    }
  }

}


// Список второго уровня в выпадающем меню
// .dropdown-submenu__list
.dropdown-submenu__list {
  padding: 0;

  li a {
    display: block;
    font-size: 1rem;
    line-height: 120%;
    color: var(--black-color);
    padding: 6px 0;
    transition: color .3s ease-in;

    @include hover {
      color: var(--second-color);
    }

    @include m-1070 {
      padding: .5rem;
      font-weight: 400;
    }
  }
}

// Заголовки в выпадающем меню
.dropdown-submenu__title {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 120%;
  color: var(--second-color);
  text-transform: uppercase;
  padding: 20px 0 10px;

  @include m-1070 {
    font-size: 1rem;
  }
}



// Обертка банера
.dropdown-submenu__banner {
  width: 100%;
  display: block;

  img {
    width: 100%;
    height: 100%;
  }
}


// Мобильное меню

.mobile-nav {
  display: none;

  @include m-1070 {
    position: absolute;
    z-index: 200;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 320px;
    background-color: var(--white-color);
    height: 100vh;
    transform: translateX(-100%);
    transition: all .3s;

    &.open {
      transform: translateX(0);
      transition: all .3s;
    }

    .nav__item {
      padding: 0 20px;
    }

    &__logo {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0.5rem 0.3rem 0.4rem;
      position: relative;
      overflow: visible;
      z-index: 500;
      border-bottom: 2px solid var(--second-color);

      a.header-logo {
        width: 140px;
      }
    }

    // mobile-nav__menu
    &__menu {
      overflow-y: auto;
      background-color: var(--grey-color);
      flex: 1;

      &::-webkit-scrollbar {
        width: .8rem;
      }

      &::-webkit-scrollbar-thumb {
        background-color: var(--blue-dark-color);
        border-radius: .5rem;
      }

      &::-webkit-scrollbar-track {
        background: var(--grey-color);
      }

      // .menu-content
      .menu-content {
        margin-right: auto;
        width: 100%;
        padding: 10px 0 22px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-self: start;
        transition: .7s ease-in-out;
        overflow-x: hidden;
      }
    }

    // mobile-nav__footer
    &__footer {
      width: 100%;
      display: block;
      text-align: center;
      padding: 1rem 1rem 1.4rem;
      position: relative;
      overflow: visible;
      z-index: 500;

      .header-btn {
        width: 100%;
        display: flex;
        justify-content: center;
        text-align: center;

        >* {
          flex: 1;
          width: 100%;
          justify-content: center;
        }
      }

      .header-socials {
        display: flex;
        justify-content: center;
        margin-top: 20px;
        gap: 1rem;

        &__icon {
          width: 40px;
          height: 40px;
        }
      }
    }

  }
}
