.courses-section {
  padding-top: 60px;
  padding-bottom: 60px;

  // .courses-section__container

  &__container {}
}

.main {

  // .main__courses-section

  &__courses-section {}
}


.courses {

  // .courses__item

  &__item+&__item {
    margin-top: 50px;
  }
}

.course {
  background-color: #fff;
  width: 100%;
  display: grid;
  grid-template-columns: 360px 1fr;
  grid-template-areas: "a b" "c c";
  align-items: center;

  @include m-960 {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "a a" "b b" "c c";
  }

  // .course__image

  &__image {
    grid-area: a;
  }

  // .course__content

  &__content {
    grid-area: b;
  }

  // .course__about

  &__about {
    grid-area: c;
  }
}

.course-image {
  position: relative;

  &__circle {
    position: absolute;
    top: -2.5rem;
    right: -2rem;
    width: 200px;
    height: 200px;
    border-radius: var(--br-50);
    border: 1px solid var(--second-bright-color);

    @include m-960 {
      right: calc(50% - 250px);
    }

    @include m-560 {
      transform: scale(0.8);
      right: -1rem;
      top: -4rem;
    }

  }

  // .course-image__tutor
  &__tutor {
    width: 100px;
    height: 100px;
    border-radius: var(--br-50);
    overflow: hidden;
    transform: translate(50%, 50%);
  }

  // .course-image__student

  &__student {
    width: 100%;

    @include m-960 {
      max-width: 300px;
      margin: 0 auto;
    }

    @include m-460 {
      max-width: 260px;
      margin: 0 auto;
    }

    img {
      width: 100%;
      aspect-ratio: 2/3;
      margin: 0 auto;
      border-radius: calc(600px / 2);
      overflow: hidden;
    }
  }

  // .course-image__desc

  &__desc {
    position: absolute;
    bottom: 2rem;
    left: 0;
    width: 290px;
    min-height: 80px;
    border-radius: 40px;
    background-color: #fff;
    display: flex;
    align-items: center;
    box-shadow: 0px 20px 20px rgba(47, 65, 88, 0.3);

    @include m-960 {
      left: calc(50% - 290px);
    }

    @include m-600 {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__desc-content {
    flex: 1;
    padding: 10px;
  }

  // .course-image__icon

  &__icon {
    flex: 0 0 80px;
    display: flex;
    align-items: center;
    justify-content: center;

    .icon-box {
      border-radius: var(--br-50);
      display: flex;
      align-items: center;
      justify-content: center;

      &--second {
        background-color: var(--second-bright-color);
      }

      &--60 {
        width: 60px;
        height: 60px;
      }
    }

    i {
      font-size: 1.5rem;
      color: var(--white-color);
    }

    &--video,
    &--computer {
      &::before {
        content: '';
        display: block;
        position: relative;
        top: 0;
        left: 10px;
        width: 60px;
        height: 60px;
        background-color: var(--second-bright-color);
        border-radius: var(--br-50);
      }

      &::after {
        content: '';
        display: block;
        position: absolute;
        top: 18px;
        left: 30px;
        width: 30px;
        height: 30px;
        transition: all .3s ease-in-out;
      }
    }

    &--video {
      &::after {
        background: url('../img/svg/icon-video.svg') 0 0 no-repeat;
      }
    }

    &--computer {
      &::after {
        background: url('../img/svg/icon-computer.svg') 0 0 no-repeat;
      }
    }

  }

  // .course-image__title

  &__title {
    font-size: .9rem;
    line-height: 120%;
    font-weight: 600;
    margin-bottom: 6px;
    color: var(--dark-color);
  }

  // .course-image__subtitle

  &__subtitle {
    font-size: .8rem;
    line-height: 120%;
    color: var(--blue-dark-color);
    letter-spacing: .04rem;
  }
}

.course-content {
  padding: 4rem 0 4rem 4rem;

  @include m-960 {
    padding: 4rem 0 1rem;
  }

  // .course-content__title

  &__title {
    margin-bottom: 20px;
  }

  // .course-content__desc

  &__desc {
    color: rgb(51, 51, 51);
    font-weight: 400;
  }

  // .course-content__tags

  &__tags {
    margin-bottom: 30px;
  }

  // .course-content__btns
  &__btns {
    margin-top: 50px;
  }

}

.tags-course {

  // .tags-course__list

  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  // .tags-course__item

  &__item {
    background-color: var(--grey-dark-color);
    padding: 2px 8px;
    border-radius: var(--br-4);
    white-space: nowrap;
    color: rgb(51, 51, 51);
    font-size: .9rem;
    letter-spacing: 1.1px;
    font-weight: 500;
    margin-bottom: 10px;
    cursor: grab;
    transition: all .3s ease-in-out;

    &:not(last-child) {
      margin-right: 10px;
    }

    i {
      font-size: .9rem;
    }

    @include hover {
      background-color: var(--blue-light-color-05);
    }

    &--active {
      background-color: var(--pink-color);
    }
  }
}


.about-course {
  padding-top: 50px;
  padding-bottom: 50px;
  // .about-course__tabs

  &__tabs {}

  // .about-course__nav

  &__nav {
    width: 100%;
    display: flex;

    @include m-768 {
      overflow-x: auto;
      padding-bottom: 20px;

      * {
        margin-right: 14px;
      }
    }
  }

  // .about-course__btn

  &__btn {
    font-size: 1rem;
    font-weight: 700;
    padding: 18px 26px 20px;
    text-align: center;
    letter-spacing: .1rem;
    background-color: var(--grey-color);
    border: 3px solid var(--grey-color);
    border-radius: var(--br) var(--br) 0 0;
    border-bottom: none;
    white-space: nowrap;
    outline: none;
    transition: background-color .1s ease-in-out, border-color .1s ease-in-out;

    @include hover {
      background-color: var(--grey-dark-color);
      border: 3px solid var(--grey-dark-color);
      border-bottom: none;
    }

    @include m-650 {
      font-size: 1.1rem;
    }

    &.active {
      pointer-events: none;
      background-color: var(--white-color);
      border-color: var(--black-color);
      z-index: 2;
      border-bottom: none;
    }

    @include m-768 {
      border: 3px solid var(--grey-color);
      border-radius: var(--br);
      padding: 18px;

      @include hover {
        border: 3px solid var(--grey-dark-color);
      }

      &.active {
        border: 3px solid var(--black-color);
      }
    }
  }

  // .about-course__content

  &__content {
    border-radius: 0 0 var(--br) var(--br);
    position: relative;
    z-index: 1;
    margin-top: -3px;
    overflow: hidden;
    background-color: var(--white-color);
    border: 3px solid var(--black-color);
    min-height: 300px;

    @include m-768 {
      border-radius: var(--br);
    }
  }

  // .about-course__item

  &__item {
    padding: 60px 40px 40px;

    @include m-460 {
      padding: 60px 12px 40px;
    }

  }
}
