.btn {
  display: inline-flex;
  align-items: center;
  background-color: transparent;
  color: var(--dark-color);
  padding: 14px 20px;
  min-width: 80px;
  border-radius: 50px;
  border: 2px solid transparent;
  transition: all .3s ease-in-out;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;

  &--link {
    display: inline-block;
    position: relative;
    text-decoration: none;
    color: var(--white-color);
    margin: 0 var(--spacing, 0px);
    transition: margin .25s;

    svg {
      width: 180px;
      height: 90px;
      position: absolute;
      left: 50%;
      bottom: 0px;
      transform: translate(-50%, 7px) translateZ(0);
      fill: none;
      stroke: var(--stroke, var(--white-color));
      stroke-linecap: round;
      stroke-width: 1px;
      stroke-dasharray: var(--offset, 69px) 278px;
      stroke-dashoffset: 361px;
      transition: stroke .25s ease var(--stroke-delay, 0s), stroke-dasharray .35s;
    }

    &:hover {
      --spacing: 4px;
      --stroke: var(--white-color);
      --stroke-delay: .1s;
      --offset: 180px;
    }
  }

  &--link-mb {
    @include m-1070 {
      color: var(--second-color);

      svg {
        stroke: var(--stroke, var(--second-color));
      }

      &:hover {
        --stroke: var(--second-color);
      }
    }
  }

  &--w {
    background-color: var(--white-color);
    border: 2px solid var(--white-color);

    @include hover {
      border: 2px solid var(--black-color);
    }
  }
// .btn--mb
  &--mb {
    @include m-1070 {
      border: 2px solid var(--black-color);

      @include hover {
        color: var(--white-color);
        background-color: var(--second-color);
        border: 2px solid var(--second-color);
      }
    }
  }

  &--comment {
    background-color: var(--black-color);
    color: var(--white-color);
    position: relative;
    transition: all .2s ease-in-out;
    z-index: 1;

    &::before {
      content: '';
      position: absolute;
      top: -20px;
      left: 30px;
      width: 30px;
      height: 40px;
      display: block;
      background: url('../img/svg/comment.svg') top / contain no-repeat;
      z-index: -1;
      transition: all .1s ease-in-out;
    }

    @include hover {
      background-color: var(--second-color);

      &::before {
        background: url('../img/svg/comment-blue.svg') top / contain no-repeat;
      }
    }
  }

  &--sm {
    padding: 10px 14px;
    border-radius: 40px;
    font-size: .9rem;
    transition: all .1s ease-in-out;

    // @include hover {
    //   // transform: scale(1.02);
    // }
  }

  &--md {
    padding: 12px 16px;
    border-radius: 40px;
    font-size: 1rem;
    transition: all .1s ease-in-out;

    @include hover {
      transform: scale(1.02);
    }
  }
&--z{
  @include hover {
  color: var(--second-color);
  }
}

  &--border {
    background-color: var(--white-color);
    color: var(--second-color);
    border: 2px solid var(--second-color);
    font-weight: 600;
  }

  &--arrow {
    background-color: var(--second-color);
    color: var(--white-color);
    position: relative;
    padding-right: 50px;
    white-space: nowrap;

    &::before {
      content: '';
      position: absolute;
      right: 10px;
      top: 50%;
      width: 25px;
      height: 20px;
      display: block;
      transform: translateY(-50%);
      background: url('../img/svg/arrow.svg') top / contain no-repeat;
    }
  }

  // btn--sec
  &--sec {
    background-color: var(--second-color);
    color: var(--white-color);


    @include hover {
      background-color: var(--second-bright-color);
    }
  }
}
