.hero {
  position: relative;
  z-index: 1;
  // .hero__wrapper

  &__wrapper {
    position: relative;
    padding: 64px 64px 56px;
    color: var(--black-color);

    @include m-1070 {
      padding: 0;
    }
  }

  // .hero__img

  &__img {
    position: absolute;
    border-radius: var(--br);
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: var(--blue-light-color);

    @include m-1070 {
      display: none;
    }

    img {
      height: 100%;
      object-position: top right;
    }
  }

  // .hero__content

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 700px;

    @include m-1070 {
      max-width: 100%;
    }

    h1 {
      text-transform: uppercase;
      font-size: 2.6rem;
      letter-spacing: 2px;
      line-height: 120%;

      @include m-1070 {
        font-size: 2rem;
      }

      @include m-520 {
        font-size: 1.6rem;
      }

      span {
        color: var(--white-color) !important;
        font-size: 3.2rem;
        text-shadow: .2rem .3rem 0px var(--blue-dark-color), .5rem .4rem 0px rgba(0, 0, 0, 0.15);

        @include m-1070 {
          font-size: 2.4rem;
        }

        @include m-520 {
          font-size: 2rem;
        }
      }
    }

    p {
      font-size: 1.6rem;
      letter-spacing: .8px;
      line-height: 120%;
      width: 100%;
      max-width: 440px;

      @include m-1070 {
        padding: 0 2rem;
        max-width: 100%;
        color: var(--second-color);
      }

    }

    ul {
      max-width: 750px;
      margin-top: 20px;
      margin-bottom: 20px;

      @include m-1070 {
        padding: 0 30px;
        max-width: 100%;
      }

      li {
        margin-bottom: .7rem;
        font-size: 1.1rem;


        &:before {
          content: '✔';
          color: var(--white-color);
          margin-right: 12px;

          @include m-1070 {
            color: inherit;
          }
        }
      }
    }
  }

  // .hero__title

  &__title {
    position: relative;
    width: 100%;
    max-width: 750px;
    margin-bottom: 22px;

    @include m-1070 {
      background-color: var(--blue-light-color);
      background-image: url('../img/decor/hero-1.webp');
      background-repeat: no-repeat;
      padding: 44px 200px 44px 44px;
      background-position: top right;
      background-size: cover;
      max-width: 100%;
      border-radius: var(--br);
      margin-bottom: 0;
    }

    @include m-520 {
      padding: 44px;
      background-image: url('../img/decor/hero-1-520.webp');
      // background-position: top right -2rem;
    }

    @include m-400 {
      background-image: none;
      background-image: url('../img/decor/hero-1-400.webp');
    }
  }

  // .hero__btns

  &__btns {

    * {
      margin-top: 1rem;
    }

    @include m-1070 {
      padding: 10px 2rem;
      max-width: 100%;
    }

    @include m-650 {
      text-align: center;

      .btn--link {
        margin-top: 40px;
      }
    }
  }
}