.method-section {
  padding: 80px 0;

  // .method-section__container

  &__container {}

  // .method-section__title

  &__title {
    margin: 0 auto 7rem;
    text-align: center;
    max-width: 1000px;

    h3 {
      color: var(--dark-color-06);
      font-size: 1.6rem !important;
      margin-top: 1rem;
      font-weight: 400;
    }

    @include m-768 {
      margin-bottom: 3rem;
    }
  }

  // .method-section__content

  &__content {}
}



.methods {
  display: flex;
  max-width: 1000px;
  gap: 2rem;
  flex-wrap: wrap;
  margin: 0 auto;

  @include m-768 {
    flex-wrap: nowrap;
    overflow-x: auto;
    padding-bottom: 2rem;
  }

  // .methods__item

  &__item {
    flex: 0 0 calc(50% - 1rem);

    @include m-768 {
      flex: 0 0 350px;
    }

    @include m-480 {
      flex: 0 0 280px;
    }
  }

  // .methods__content
  &__content {
    width: 100%;
    position: relative;
    border: 1px solid var(--grey-dark-color);
    padding: 56px 75px;
    border-radius: var(--br-2);
    box-shadow: var(--box-shadow-min-2);
    transition: box-shadow .3s ease-in-out;

    @include m-480 {
      padding: 36px 45px;
    }

    @include hover {
      box-shadow: var(--box-shadow-min);
    }

    &--top {
      transform: translateY(-4rem);

      @include m-768 {
        transform: translateY(0);
      }
    }

    &--top-2 {
      transform: translateY(-5rem);

      @include m-768 {
        transform: translateY(0);
      }
    }

    img {
      width: 72px;
      height: 72px;
      margin-bottom: 1rem;
    }

    h3 {
      font-size: 1.1rem;
      color: var(--second-bright-color);
      margin-bottom: 0.5rem;
    }

    h4 {
      font-size: .9rem;
    }

    p {
      font-size: .9rem;
      color: var(--dark-color-06);
    }
  }
}
