.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  overflow: hidden;
  z-index: 1000;
  transition: all 1s ease-in;
  opacity: 1;
  visibility: visible;
}
.preloader.done {
  opacity: 0;
  visibility: hidden;
  z-index: -1000;
}
.preloader__loader, .preloader__percents {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.preloader__percents,
.preloader #percents {
  font-size: 1.7rem;
  color: var(--second-color);
}

.wave {
  position: relative;
  border-radius: var(--br-50);
  width: 100px;
  height: 100px;
  opacity: 1;
}
.wave:before, .wave:after {
  content: "";
  position: absolute;
  border-radius: var(--br-50);
  border: 1px solid var(--second-color);
  width: 100%;
  height: 100%;
  left: 0px;
}
.wave:before {
  animation: wave-outer 0.6s infinite linear;
  transform: scale(1);
  opacity: 1;
}
.wave:after {
  animation: wave-inner 0.6s infinite linear;
  transform: scale(0);
  opacity: 0;
}
.wave__icon {
  position: absolute;
  left: 50%;
  top: -50%;
  width: 90px;
  height: 90px;
  object-fit: contain !important;
}

@keyframes wave-outer {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(1.5);
  }
}
@keyframes wave-inner {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}
.overlay.open {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s;
}

