.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  // background: url('../img/bg/bg.webp') center no-repeat;
  overflow: hidden;
  z-index: 1000;
  transition: all 1s ease-in;
  opacity: 1;
  visibility: visible;

  &.done {
    opacity: 0;
    visibility: hidden;
    z-index: -1000;
  }

  &__loader,
  &__percents {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

