}


.wave {
  position: relative;
  border-radius: var(--br-50);
  width: 100px;
  height: 100px;
  opacity: 1;

  &:before,
  &:after {
    content: '';
    position: absolute;
    border-radius: var(--br-50);
    border: 1px solid var(--second-color);
    width: 100%;
    height: 100%;
    left: 0px;
  }

  &:before {
    animation: wave-outer 0.6s infinite linear;
    transform: scale(1);
    opacity: 1;
  }

  &:after {
    animation: wave-inner 0.6s infinite linear;
    transform: scale(0);
    opacity: 0;
  }

  &__icon {
    position: absolute;
    left: 50%;
    top: -50%;
    width: 90px;
    height: 90px;
    object-fit: contain !important;
  }
}

@keyframes wave-outer {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(1.5);
  }
}

@keyframes wave-inner {
  from {
    opacity: 0;
    transform: scale(0.5);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}