:root {
  --content-width: 1352px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --minWidth: 320;
  // Ширина полотна (макета) Максимальное значение окна
  --maxWidth: 1920;

  // FONT
  --font-family: "Platform", sans-serif;
  --font-medium: 0.8em;
  --font-lg: 1.125rem;

  // BG
  --bg-grid-slate: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 32 32' width='32' height='32' fill='none' stroke='rgb(15 23 42 / 0.04)'%3e%3cpath d=' M0 .5H31.5V32'/%3e%3c/svg%3e");

  --br: 1.2rem;
  --br-2: calc(var(--br) / 2);
  --br-3: calc(var(--br) / 3);
  --br-4: calc(var(--br) / 4);
  --br-50: 50%;

  // COLORS
  --white-color: #fff;
  --black-color: #101010;

  --dark-color: rgba(44, 58, 75, 1);
  --dark-color-08: rgba(44, 58, 75, 0.8);
  --dark-color-06: rgba(44, 58, 75, 0.6);
  --dark-color-04: rgba(44, 58, 75, 0.4);

  --violet: #3f3a64;

  --second-bright-color: #0270c1;
  --second-color: #01487a;
  --second-color-07: rgba(1, 72, 122, .7);
  --second-color-08: rgba(1, 72, 122, .8);

  --blue-light-color: rgb(170, 197, 213);
  --blue-light-color-05: rgba(170, 197, 213, 0.5);
  --blue-light-color-02: rgba(170, 197, 213, 0.2);
  --blue-dark-color: rgb(133, 162, 182);

  --yelow-color: rgb(253, 193, 70);
  --yelow-color-04: rgba(253, 192, 70, 0.4);
  --yelow-color-07: rgba(253, 192, 70, 0.7);
  --yelow-color-08: rgba(253, 192, 70, 0.8);
  --pink-color: rgb(254, 205, 201);
  --pink-light-color: rgb(255, 233, 231);
  --pink-color-04: rgba(254, 205, 201, 0.6);
  --grey-color: rgb(246, 246, 246);
  --grey-color-07: rgba(235, 235, 235, .7);
  --grey-dark-color: rgb(231, 233, 237);

  --red-dark-color: rgb(176, 13, 34);

  --box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.1);
  --box-shadow-min:
    0 3.4px 3.7px rgba(0, 0, 0, 0.017),
    0 3.3px 4px rgba(0, 0, 0, 0.024),
    0 6.3px 7.5px rgba(0, 0, 0, 0.03),
    0 11.2px 13.4px rgba(0, 0, 0, 0.036),
    0 20.9px 25.1px rgba(0, 0, 0, 0.043),
    0 50px 60px rgba(0, 0, 0, 0.06);
    --box-shadow-min-2:
        0 3.4px 3.7px rgba(0, 0, 0, 0.01),
        0 3.3px 4px rgba(0, 0, 0, 0.01),
        0 6.3px 7.5px rgba(0, 0, 0, 0.01),
        0 11.2px 13.4px rgba(0, 0, 0, 0.01),
        0 20.9px 25.1px rgba(0, 0, 0, 0.01),
        0 50px 60px rgba(0, 0, 0, 0.01);
}


